export default class Task  {
    id: string; // 
    content: string; // 
    status: number; // 
    project_id: string;// 
    parent_id: string | null;
    create_time: Date; //
    due_date: string | null; // 
    finishTime: Date | null; // 
    tags: string[];
    priority: number; //
    collapsed: number;
    user_id: string|null;

    constructor() {
       this.id = "";
       this.content = "";
       this.status = -1;
       this.project_id = "-1";
       this.create_time = new Date();
       this.due_date = null;
       this.parent_id = null;
       this.finishTime = null;
       this.tags = [];
       this.priority = 1;
       this.collapsed = 0;
       this.user_id = null;
    }
 }
 